.section-1 {
  background-image: url('../img/header-bg.png');
  padding-top: 191px;
  background-repeat: no-repeat;
  background-size: cover;
  padding-bottom: 280px;
  background-position: top center;
  overflow: hidden;
  color: #fff;

  h1 {
    font-size: 48px;
    font-weight: bold;
    line-height: 58px;
  }

  p {
    margin-top: 27px;
    font-size: 30px;
    line-height: 40px;
    margin-bottom: 23px;
  }

  ul {
    display: flex;
    margin-bottom: 60px;

    li {
      display: flex;
      align-items: center;
      font-size: 20px;
      font-weight: 500;
      line-height: 26px;
      margin-right: 33px;

      img {
        margin-right: 15px;
      }
    }
  }

  .main-btn {
    width: 472px;
    height: 104px;
    border-radius: 52px;
    font-size: 18px;
    font-size: bold;
    line-height: 26px;
  }
}